<template>
  <div>
    <headerBar :header-data="headerData" />
    <div v-if="xm_fhdh.length>0" class="container-tab">
      <van-sticky class="tabs-sticky">
        <van-tabs v-model="active" swipeable animated>
          <van-tab title="编辑发货到货">
            <div class="container-mini">
              <div class="content">
                <div class="common-main box">
                  <div v-for="(item,index) of xm_fhdh" :key="index">
                    <div v-if="item.type!=='hidden'">
                      <van-field
                        v-if="item.type==='text'"
                        v-model="item.value"
                        :label="item.showName"
                        input-align="right"
                        label-width="100"
                        readonly
                      />
                      <van-field
                        v-if="item.type==='calendar'"
                        v-model="item.value"
                        label-width="100"
                        input-align="right"
                        readonly
                        :label="item.showName"
                      />
                      <van-field
                        v-if="item.type==='select'"
                        v-model="item.value"
                        readonly
                        input-align="right"
                        label-width="100"
                        :label="item.showName"
                      />
                      <van-field
                        v-if="item.type==='label'"
                        v-model="item.value"
                        readonly
                        input-align="right"
                        label-width="100"
                        :label="item.showName"
                        placeholder="草稿"
                      />
                      <div v-if="item.type==='file'" class="page-list">
                        <div class="pagelist-left">附件</div>
                        <div class="pagelist-right">
                          <XyUploadPlus
                            ref="uploadEl"
                            style="width:100%;"
                            filed="fj"
                            :is-show="false"
                            :filegroup="filegroup"
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </van-tab>
          <van-tab title="商品信息">
            <div v-if="fhmxList.length>0" class="container-mini">
              <div v-for="(items,indexs) of fhmxList" :key="indexs" class="content">
                <div class="common-main box">
                  <!-- 循环列表 begin -->
                  <div class="general-list">
                    <div class="general-item" @click="showSpxzPopup(indexs)">
                      <div class="general-tit">{{ items.htmc }}</div>
                      <div class="general-info"><span><em>订单编号：</em>{{ items.htbh }}</span></div>
                    </div>
                    <div class="general-arrow"><i class="iconfont iconarrowright" /></div>
                  </div>
                  <!-- 循环列表 end -->
                </div>
              </div>
            </div>
            <div v-else class="container-mini">
              <div class="content">
                <div class="common-main box">
                  <div class="cart-empty">
                    <div class="cart-empty-btn"><van-button size="small" plain type="info" @click="goAddSp()">去添加商品</van-button></div>
                  </div>
                </div>
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </van-sticky>
    </div>
    <div class="common-space" />
    <div class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="goShglLzrz()">流转日志</van-button></li>
      </ul>
    </div>

    <!-- 选择商品弹出层 beign -->
    <van-popup
      v-model="SpxzShow"
      :get-container="getContainer"
      closeable
      position="bottom"
      close-icon-position="top-left"
      :style="{ height: '90%',background:'#f9f9f9' }"
    >
      <div class="popup-title">商品信息</div>
      <div v-if="fhmxList && fhmxList[mxIndex]" class="popup-content full">
        <!-- 商品列表 begin -->
        <div v-for="(item,index) of fhmxList[mxIndex].mxList" :key="index" class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title-check">
            <div class="check-info">{{ item.spmc }}</div>
          </div>
          <!-- 通用标题 end -->
          <div class="page-list">
            <div class="pagelist-left">订单数量</div>
            <div class="pagelist-right">{{ item.sl }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">交货数量</div>
            <div class="pagelist-right">{{ item.bcsl }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">单价（含税）元</div>
            <div class="pagelist-right">{{ item.dj }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">计量单位</div>
            <div class="pagelist-right">{{ item.jldw }}</div>
          </div>
        </div>
        <!-- 商品列表 end -->
      </div>
    </van-popup>
    <!-- 选择商品弹出层 end -->

  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import XyUploadPlus from '@/components/upload/uploadPlus'
export default {
  components: {
    headerBar,
    XyUploadPlus
  },
  data() {
    return {
      zt: '',
      shsj: '',
      dhlx: '',
      SpxzShow: false,
      headerData: {
        title: '送货信息',
        leftUrl: 'back',
        leftText: ' '
      },
      fhbh: '',
      id: '',
      fhmxList: [],
      checkedMxList: [], // 选中的商品明细信息
      mxIndex: 0,
      mxList: [],
      spxxList: [],
      gysList: [],
      canChange: false,
      pageName: '', // 当前tab页页面
      fileChangeInfo: {},
      filegroup: '',
      fhsj: '',
      yjdhsj: '',
      YjdhsjShow: false,
      date1: '',
      date: '',
      wllxShow: '',
      wllx: '',
      bcshsl: '',
      checked: '',
      fhdbh: '',
      active: '',
      fhdmc: '',
      wlgsmc: '',
      ydh: '',
      fhdd: '',
      wllxr: '',
      wllxfs: '',
      xmmcvalue: '',
      xmbhvalue: '',
      FhsjShow: false,
      showDelete: false,
      showSearch: false,
      result: [],
      result2: [],
      fileList: [
        { url: 'https://img.yzcdn.cn/vant/leaf.jpg' }
      ],
      searchPick: {
        PickTitle: '请选择物流类型',
        PickColumns: ''
      },
      activeNames: ['1'],
      xm_fhdh: []
    }
  },
  computed: {

  },
  created() {
    this.getWllx()
    if (this.$store.state.fhgl.fhbh) {
      this.fhbh = this.$store.state.fhgl.fhbh
    }
    this.getGysFhxx()
  },

  methods: {
    getContainer() {

    },
    // 获取物流类型
    getWllx() {
      this.$store
        .dispatch('fhgl/getWllxAction')
        .then(res => {
          const maps = []
          for (var key in res) {
            maps.push({ 'value': key, 'label': res[key] })
          }
          this.searchPick.PickColumns = maps
        })
        .catch((error) => {
          this.$toast.fail(error)
        })
    },
    getGysFhxx() {
      this.$store.dispatch('fhgl/getXm_fhdhmxListAction', {
        fhbh: this.fhbh
      }).then((res) => {
        this.xm_fhdh = res.xm_fhdh
        for (let i = 0; i < res.fhmxList.length; i++) {
          const arr = {}
          arr.state = false
          for (let j = 0; j < res.fhmxList[i].length; j++) {
            const item = res.fhmxList[i][j]
            const key = item.name
            const value = item.value
            arr[key] = value
            if (item.mxList) {
              const array = []
              for (let k = 0; k < item.mxList.length; k++) {
                const element = item.mxList[k]
                const a = {}
                a.state = false
                for (let x = 0; x < element.length; x++) {
                  const temp = element[x]
                  const key = temp.name
                  const value = temp.value
                  a[key] = value
                }
                array.push(a)
              }
              arr['mxList'] = array
            }
          }
          this.fhmxList.push(arr)
        }
        for (let i = 0; i < this.xm_fhdh.length; i++) {
          const item = this.xm_fhdh[i]
          if (item.type === 'calendar' && item.value) {
            item.reqValue = item.value
            item.value = item.value + ''
            item.value = item.value.slice(0, 4) + '-' + item.value.slice(4, 6) + '-' + item.value.slice(6, 8)
          }
          if (item.type === 'file' && item.value) {
            this.filegroup = item.value
          }
          if (item.type === 'select' && item.value) {
            for (let i = 0; i < this.searchPick.PickColumns.length; i++) {
              if (this.searchPick.PickColumns[i].value === item.value) {
                item.reqValue = item.value
                item.value = this.searchPick.PickColumns[i].label
              }
            }
          }
        }
      })
    },
    showSpxzPopup(index) {
      this.mxIndex = index
      this.SpxzShow = true
    },
    goShglLzrz() {
      this.$store.commit('fhgl/SET_FHBH', this.fhbh)
      this.$router.push({
        name: 'ShglLzrz'
      })
    }
  }

}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
